import QQMapWX from '../../util/map'
import mkbSite from '../../components/mkb-site/mkb-site'

const wxMap = new QQMapWX({
	key: 'LXNBZ-P2MCX-IAK4F-7NJRT-PLJ4Q-ATF45'
})

import Constant from '../../util/constant.js'
import Messages from '../../util/messages.js'
import mkbDropdown from '../../components/mkb-dropdown/mkb-dropdown.vue'

export default {
	components: {
		mkbSite,
		mkbDropdown
	},
	data() {
		return {
			searchBar: {
				background: "rgba(0,0,0,0.05)",
				placeholder: "搜索",
				borderRadius: '28.98rpx',
				fontSize: '25.36rpx',
				color: 'rgba(0,0,0,0.40)'
			},
			siteName: "天府新谷",
			address: "",
			stage: Constant.siteBar[3].type,
			//1 正序 2 逆序
			orderType: 2,
			//1 面积 2 预算金额 3距离
			sortType: 3,
			pageNo: Constant.page.pageNo,
			pageSize: Constant.page.pageSize,
			longitude: Constant.location[0],
			latitude: Constant.location[1],
			siteBar: Constant.siteBar,
			siteList: [],
			clickMore: true,
			dropDownShow: false,
			dropDownItems: Constant.items
		}
	},
	
	onShow() {
		/**
		 * 监听地址选择后回调
		 */
		uni.$on("site", (data) => {
			let pos = data.site.location.split(",");
			this.siteName = data.site.name;
			this.longitude = pos[0] ? pos[0] : Constant.location[0];
			this.latitude = pos[1] ? pos[1] : Constant.location[1];
			this.resetData();
			uni.$off("site");
		})
	},

	//底部获取
	onReachBottom() {
		// setTimeout(() => {
		if (this.clickMore) {
			this.pageNo++;
			this.loadData();
		}else{
			uni.showToast({
				icon:"none",
				title: Mesages.LOADING_FINISH
			})
		}
		// }, 1000);			
	},

	// onPullDownRefresh() {
	// 	// setTimeout(() => {
	// 		uni.stopPullDownRefresh();
	// 		if(this.clickMore){
	// 			this.pageNo ++;
	// 			this.loadData();
	// 		}
	// 	// }, 1000);	
	// },

	onLoad(option) {
		//定位授权
		let that = this;

		uni.showLoading({
			title: "加载中"
		});
		uni.getSetting({
			success: (res) => {
				//未授权地理定位
				uni.hideLoading();
				if (!res.authSetting["scope.userLocation"]) {
					uni.showLoading({
						title: "授权中"
					});
					uni.authorize({
						scope: 'scope.userLocation',
						success: (res) => {
							uni.hideLoading();
							that.location();
						},
						fail: (err) => {
							uni.hideLoading();
							uni.showModal({
								title: "提示",
								content: Messages.FAIL_AUTH,
								showCancel: false
							})
						}
					});
				} else {
					that.location();
				}
			},
			fail: (err) => {
				uni.hideLoading();
				uni.showModal({
					title: "提示",
					content: Messages.NO_AUTH,
					showCancel: false
				})
			}
		})









	},

	methods: {
		/**
		 * 自定义的搜索框
		 * @param {Object} e
		 */
		confirmInput(e) {
			this.address = e.value;
			this.resetData();
		},

		/**
		 * 自主定位
		 */
		location() {
			let that = this;
			uni.showLoading({
				title: "定位中"
			});
			uni.getLocation({
				type: 'wgs84',
				geocode: true,
				success: (res) => {
					uni.hideLoading();
					that.longitude = res.longitude;
					that.latitude = res.latitude;
					that.rendereGeocoder([res.latitude, res.longitude].join(","));
					that.loadData();
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showModal({
						title: "提示",
						content: Messages.FAIL_LOCATION,
						showCancel: false
					})
				}
			});
		},

		/**
		 * 根据经纬坐标显示名称
		 * @param {Object} geocodes
		 */
		rendereGeocoder(geocodes) {
			uni.showLoading({
				title: "数据解析中"
			});

			wxMap.reverseGeocoder({
				location: geocodes,
				success: (res) => {
					uni.hideLoading();
					this.siteName = res.result.address_component.street;
				},
				fail: (err) => {
					console.log(err);
					uni.hideLoading();
					uni.showModal({
						title: "提示",
						showCancel: false,
						content: Messages.FAIL_RESOLVE
					})
				}
			})
		},

		/**
		 * 接口拉取数据
		 */
		loadData() {
			let that = this;

			uni.showLoading();
			let params = {
				longitude: that.longitude,
				latitude: that.latitude,
				stage: that.stage,
				orderType: that.orderType,
				pageNo: that.pageNo,
				pageSize: that.pageSize
			};

			if (that.sortType) {
				params["sortType"] = that.sortType;
			};
			if (that.address) {
				params["address"] = that.address;
			}
			that.saveLocation(params.latitude, params.longitude);

			that.$http.getSiteResults(params).then(res => {
				uni.hideLoading();
				if (res && res.code == 200) {
					// console.log(res.data);
					if (res.data && res.data.length > 0) {
						res.data.forEach(item => {
							item.coverImgs = item.coverImgs && item.coverImgs.length > 0 ? item.coverImgs : [Constant.defaultImage];
							that.siteList.push(item);
						})
					} else {
						that.clickMore = false;
					}
				} else {
					uni.showToast({
						icon: "none",
						title: res.msg || Messages.FAIL_INFO
					});
				}
			}).catch(err => {
				uni.showToast({
					icon: "none",
					title: err.msg || Messages.FAIL_INFO
				});
			})


		},

		//改变序列查询 refs
		showDropdown(e) {
			console.log(e);
			this.dropDownShow = !this.dropDownShow;
			console.log("this show drop down is===" + this.dropDownShow);
			console.log(this.dropDownItems);
		},

		/**
		 * 弹出框选择后反馈
		 * @param {Object} e
		 */
		selectItem(e) {
			if (e.item) {
				this.dropDownItems.forEach(d => {
					d.actived = (e.item.value === d.value);
				});
				//1 正序 2 逆序
				// orderType: 1,
				//1 面积 2 预算金额 3距离
				// sortType: 3,
				this.sortType = this.getSortType(e.item.value);
				this.orderType = (e.item.value % 2 === 0) ? 2 : 1;
				this.resetData();
			}
			this.dropDownShow = e.show;
		},

		/**
		 * 整理查询参数
		 * @param {Object} val
		 */
		getSortType(val) {
			switch (val) {
				case 0:
				case 1:
					return 3;
				case 2:
				case 3:
					return 1;
				case 4:
				case 5:
					return 2;
				default:
					return 3;
			}
		},

		/**
		 * 根据不同状态查询
		 * @param {Object} st 状态
		 */
		changeState(st) {
			this.stage = st;
			this.resetData();
		},

		/**
		 * 重置条件后查询数据
		 */
		resetData() {
			this.pageNo = Constant.page.pageNo;
			this.clickMore = true;
			this.siteList = [];
			this.loadData();
		},

		/**
		 * 去定位其它地址
		 */
		toSiteAddress() {
			uni.navigateTo({
				animationType: "slide-in-left",
				url: `../site-address/site-address?latitude=${this.latitude}&longtitude=${this.longitude}`
			})
		},

		/**
		 * 到案例详情
		 * @param {Object} quoteId
		 */
		toDetail(quoteId) {
			this.$openPage({
				name: "example",
				query: {
					quoteId
				}
			})
		},
		/**
		 * 保存定位信息
		 * @param {Object} lat 经纬 度
		 * @param {Object} lng
		 */
		saveLocation(lat, lng) {
			uni.setStorageSync("userLocationInfo", {
				latitude: lat,
				longitude: lng
			});
		}

	}
}
